import Vue from 'vue';
import VueRouter from 'vue-router';
import NotFoundPage from './views/NotFoundPage.vue';

Vue.use(VueRouter);

const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return routerPush.call(this, location).catch(error => error);
}

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{ path: '*', component: NotFoundPage },
		{
			path:'/login',
			name: 'login',
			component: () => import('./views/LoginPage.vue'),
			meta: {
			    title: '登录',
			},
		},
		{
			path:'/',
			redirect: '/type',
			name: 'home',
			component: () => import('./views/LayoutPage.vue'),
			children:[
				{
					path:'/type',
					name: 'type',
					component: () => import('./views/TypePage.vue'),
					meta: {
					    title: '电影类型',
						breadcrumb: [
						    {name: '首页', link: '/'},
						    {name: '电影类型'}
						]
					},
				},
				{
					path:'/country',
					name: 'country',
					component: () => import('./views/CountryPage.vue'),
					meta: {
					    title: '电影地区',
						breadcrumb: [
						    {name: '首页', link: '/'},
						    {name: '电影地区'}
						]
					},
				},
				{
					path:'/movie',
					name: 'movie',
					component: () => import('./views/MoviePage.vue'),
					meta: {
					    title: '电影管理',
						breadcrumb: [
						    {name: '首页', link: '/'},
						    {name: '电影管理'}
						]
					},
				},
				{
					path:'/creat_movie',
					name: 'creat_movie',
					component: () => import('./views/MovieDataPage.vue'),
					meta: {
					    title: '电影管理',
						breadcrumb: [
						    {name: '首页', link: '/'},
						    {name: '新建电影'}
						]
					},
				},
				{
					path:'/edit_movie/:id',
					props: true,
					name: 'edit_movie',
					component: () => import('./views/MovieDataPage.vue'),
					meta: {
					    title: '电影管理',
						breadcrumb: [
						    {name: '首页', link: '/'},
						    {name: '新建电影'}
						]
					},
				},
			]
		}
	]
});

router.beforeEach((to, from, next)=>{
	if(to.name === 'login'){
		next();
	}else{
		const token = localStorage.getItem("token");
		if(!token || token.length === 0 || (typeof token !== 'string')){
			next('/login');
		}else{
			next();
		}
	}
});

export default router;
